  }

  .checkoutButton {
   
      background-color: #ff4c4c; /* Green */
      border: none;
      color: white;
      padding: 15px 32px;
      position:relative;
      top:50px;
      width: 150px;
      height: 50px;
      text-align: center;
      text-decoration: none;
      display: inline-block;
      font-size: 16px;
  }

  .addButton {
    background-color: #ff4c4c; /* Green */
    border: none;
    color: white;
    position:relative;
    width: 150px;
    height: 25px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;

  }
  .removeButton {
    background-color: #ff4c4c; /* Green */
